import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
* Fetches actions
* @returns {Promise} response - a promise of a actions arrays
*/
export function getActions(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/actions?limit=${limit}&offset=${offset}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches an action
* @param {String} id - id of action
* @returns {Promise} response - a promise of a action item
*/
export function getAction(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/actions/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}


/**
* Creates an action
* @returns {Promise} response - a promise of a action to be created
*/
export function createAction(data) {
	if (isLoggedIn()) {
		return axios.post('/v1/actions', data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}


/**
* Updates an action
* @param {String} id - action Id
* @param {String} data - action data object
* @returns {Promise} response - a promise of a action to be updated
*/
export function updateAction(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/actions/${id}`, data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}
