<template>
	<div>
		<template v-if="loadingAction">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<h3 class="user-profile__name">Action Detail Section</h3>
					</div>
					<div class="user-profile__item">
						<h5 class="user-profile__section__title">Action Title:</h5>
						<p>{{ itemData.title }}</p>
					</div>
					<div class="user-profile__item">
						<h5 class="user-profile__section__title">Action Link:</h5>
						<p>{{ itemData.link }}</p>
					</div>
				</div>

				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="false">Edit Action</a>
						</li>
					</ul>
					<div class="tab-pane fade show active user-profile__tabs__section" id="activity" role="tabpanel" aria-labelledby="activity-tab">
						<div class="settings">
							<form
								method="post"
								@submit.prevent="updateAction">
								<div class="settings__field">
									<div class="form-row">
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="jobTitle">Title</label>
												<input
													:class="[{'is-invalid': errors.title}]"
													@keydown="clearErrors($event.target.name)"
													v-model="itemData.title"
													name="title"
													value=""
													type="text"
													class="form-control"
													id="jobTitle">
											</div>
											<div class="invalid-feedback">
												{{ errors.title }}
											</div>
										</div>
									</div>
									<div class="form-row">
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="jobTitle">Link</label>
												<input
													:class="[{'is-invalid': errors.link}]"
													@keydown="clearErrors($event.target.name)"
													v-model="itemData.link"
													name="title"
													value=""
													type="text"
													class="form-control"
													id="jobTitle">
											</div>
											<div class="invalid-feedback">
												{{ errors.link }}
											</div>
										</div>
									</div>
								</div>
								<div class="settings__primary">
									<button
										class="btn btn__save"
										type="button"
										name="button"
										@click.prevent="updateAction"
										v-bind="{'disabled': loading}">
										Save
									</button>
									<Loader  v-if="loading"/>
								</div>
							</form>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { updateAction, getAction } from '../../utils/api/action';
import { success, fail } from '../../utils/toast';
import action from '../../utils/validations/action';

export default {
	name: 'ActionDetail',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loadingAction: true,
			actionLoaded: false,
			loading: false
		};
	},
	components: {
		Loader
	},
	created() {
		this.getAction();
	},
	methods: {
		getAction() {
			getAction(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.actionLoaded = true;
						return true;
					}
					this.actionLoaded = false;
					return false;
				})
				.catch((error) => {
					this.actionLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingAction = false;
				});
			return true;
		},
		updateAction() {
			const data = this.itemData;
			delete data.created_at;
			delete data.updated_at;
			delete data.id;
			this.validate(data, action.update);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			updateAction(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Action has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	}
};
</script>

<style lang="scss">
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.settings {
	&__field {
		margin-top: 30px;
	}
	&__value {
		padding: 0;
		background: #fff;
		input {
		background: #fafafa;
		border: none;
		font-size: 14px;
		height: 36px;
		}
		label {
		font-weight: bold;
		margin-bottom: 0;
		}
	}
}
</style>
